/* eslint-disable */

import { render } from '@testing-library/react';
import React, { useState } from 'react';
import './App.css';


function App() {

  //    : immutable data

  // ES6 destructuring
  // var [a,b] = [10, 100];

  // ES6 arrow function
  // onClick={ () => { } }

  //     

  // state 1.     2. useState()  .
  // state  HTML   .     state .
  
  // [a,b] a:useState b:useState   (state )
  let [subject, setSubject] = useState(['  ', ' ', '']); 
  let [ddabong, setDdabong] = useState([0, 0, 0]);

  let [modal, setModal] = useState(false);
  let [title, setTitle] = useState(0);
  let [invalue, setInvalue] = useState('');
  let [daylist, setDaylist] = useState(['2022 1 3', '2021 12 27', '2021 10 31']);

  // react  :   
  // 1.array      
  // 2.   array   
  // 3. return   array 
  [1,2,3].map(function(){
    
  })


  function textChange() {
    //  state  .   . state     deep copy .
    var newArray = [...subject]
    newArray[0] = '  ';
    setSubject( newArray );
  }

  function switchModal() {
    setModal(!modal);
  }
  
  function clickDdabong(i) {
    let newDdabong = [...ddabong];
    newDdabong[i] +=1;
    setDdabong(newDdabong);
  }

  function modifySubject(){
    var newSubject = [...subject];
    newSubject[0] = '  ';
    setSubject(newSubject);
  }

  function addBlog() {
    if (invalue == '') {
      alert(' !');
    } else {
      let newBlogSubject = [...subject];
      newBlogSubject.unshift(invalue);
      setSubject(newBlogSubject);  

      let newDdabongList = [...ddabong];
      newDdabongList.unshift(0);
      setDdabong(newDdabongList);

      let newDayList = [...daylist];
      let today = new Date();
      let year = today.getFullYear();
      let month = (today.getMonth()+1);
      let day = today.getDate();
      let dayString = year + ' ' + month + ' ' + day + '';
      newDayList.unshift(dayString);
      setDaylist(newDayList);
    }
  }

  function deleteBlog(n) {
    let newBlogList = [...subject];
    // for (let i=0; i<subject.length; i++) {
    //   if (i != n) {
    //     newBlogList.push(subject[i])
    //   }
    // }
    newBlogList.splice(n, 1);
    setSubject(newBlogList);
  }


  return (
    <div className="App">
      
      <div className="black-nav">
        <div> Blog</div>
      </div>

      {
        subject.map(function(a, i){
          return (
            <div className="list">
            <h3 onClick={()=>{setModal(!modal); setTitle(i);}}> { a } { i } <span onClick={ () => {clickDdabong(i)} }></span> {ddabong[i]} </h3>
            <p>{daylist[i]}</p>
            <button onClick={()=>deleteBlog(i)}></button>
            <hr/>
          </div>
          )
        })
      }

      <input onChange={(e)=>setInvalue(e.target.value)}></input>
      <button onClick={()=>addBlog()}></button>

      {
        modal == true ? <Modal title={title} subject={subject} day={day} color={'pink'} modifySubject={modifySubject} /> : null
      }

      <Profile/>

    </div>
  );
}

//  
// 1. component   2. return()     
// : state   .  component  state props  .
function Modal(props){

  return(
    <div className='modal' style={{background: props.color}}>
      <h2>{props.subject[props.title]}</h2>
      <p>{props.day}</p>
      <p></p>
      <button onClick={() => {props.modifySubject()}}></button>
    </div>
  )
}

//  
class Profile extends React.Component {
  constructor() {
    super();
    this.state = { name : 'Kim', age : 30}
  }
  changeName = () => {
    this.setState( {name: 'Chang'} )
  }

  render(){
    return (
      <div>
        <div> </div>
        <p>  {this.state.name} .</p>
        <button onClick={ this.changeName.bind(this) }></button>
      </div>
    )
  }
}


export default App;


